* {
  padding: 0;
  margin: 0;
  font-family: 'Inter', sans-serif;
}

:root {
  --accent: #226997;
  --LimeGreen: hsl(163, 72%, 41%);
  --BrightRed: hsl(356, 69%, 56%);
  --Facebook: hsl(195, 100%, 50%);
  --Twitter: hsl(203, 89%, 53%);
  --Instagram:  hsl(37, 97%, 70%);


  --YouTube: hsl(348, 97%, 39%);
  --ToggleDark: linear gradient hsl(210, 78%, 56%) to #3eda82;
  --ToggleLight: hsl(230, 22%, 74%);
/*   DarkTheme */
  --VeryDarkBlueBG: hsl(230, 17%, 14%);
  --VeryDarkBlueTopBGPattern: hsl(232, 19%, 15%);
  --DarkDesaturatedBlueCardBG: hsl(228, 28%, 20%);
  --DesaturatedBlueText: hsl(228, 34%, 66%);
  --WhiteText: hsl(0, 0%, 100%);
/* Light Theme */
  --WhiteBG: hsl(0, 0%, 100%);
  --VeryPaleBlueTopBGPattern: hsl(225, 100%, 98%);
  --LightGrayishBlueCardBG: hsl(227, 47%, 96%);
  --DarkGrayishBlueText: hsl(228, 12%, 44%);
  --VeryDarkBlueText: hsl(230, 17%, 14%);
}




.BCont {
  display: grid;
  grid-template-rows:  25vh; 
  grid-template-columns: 25vh 25vh 25vh 25vh ;
  grid-column-gap: 25vh;
  justify-content: center;
}


body {
    background-color: var(--VeryDarkBlueBG);
}

.box4 .todayNumber {
  color: var(--BrightRed);
}


.box1 {
  border-top: 5px var(--Facebook) solid;
  width: 29vh;
  border-radius: 5px;
}

.box2Test {
  background-color: rgb(211, 2, 2);
}

.box2 {
  border-top: 5px var(--Twitter) solid;
  width: 29vh;
  border-radius: 5px;
}


.box3 {
  border-top: 5px var(--Instagram) solid;
  width: 29vh;
  border-radius: 5px;
}


.box4 {
  border-top: 5px var(--YouTube) solid;
  width: 29vh;
  border-radius: 5px;
}







.negative .todayNumber{
  color: var(--BrightRed)
}


.positive .todayNumber{
  color: var(--LimeGreen)
}

.SCont {
  display: grid;
  grid-template-rows: 17vh; 
  grid-template-columns: 25vh 25vh 25vh 25vh ;
  grid-column-gap: 25vh;
  justify-content: center;
}


@media only screen and (max-width: 375px) {
  .BCont {
    display: flex;
    flex-direction: column;
    padding: 1vh 2vh 2vh 2vh;
    justify-items: center;
  }

  .box1, .box2, .box3, .box4 {
    border-top: none;
  }

  .SCont {

    display: flex;
    flex-direction: column;
    padding: 1vh 1vh 1vh 1vh;
    justify-items: center;
  }
}

